*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #f1f5f8;
    color: #222;
}
.booklist{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}
@media screen and (min-width:760px) {
    .booklist{
        grid-template-columns: repeat(3, 1fr);
    }
}
.book{
    background: #fff;
    border-radius: 1rem;
    padding: 1rem 2rem;
    
}

.book h4{
    margin-top: 0.5rem;
    color: #17528a;
    font-size: 80%;
}
.book h5{
    color: #617d98;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}
.book p{
    margin-top: 00.5rem;
}
article .book img{
    height: 50%;
    width: 100%;
}
article{
    height: fit-content;
}